html {
  --blue-color: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.header__link {
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list :nth-child(n+2) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--blue-color);
}

.is-active {
  color: var(--blue-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
